@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*,*::before,*:after{margin:0;padding:0;box-sizing:border-box;font-family:"Montserrat",sans-serif;font-size:1rem;color:#292b2c}#root{min-height:100vh;display:flex;justify-content:center;align-items:center;grid-gap:2rem;gap:2rem;background-color:#f7f7f7}#root{flex-direction:column}h1{font-size:1.4rem}video{max-width:50vw;border-radius:4px;display:none}video[src]{display:block}a{text-decoration:none;border-bottom:1px dashed #0275d8;color:#0275d8;display:none}a[download]{display:block}button{padding:.75rem 1.25em;border:none;outline:none;border-radius:4px;background-color:#0275d8;color:#f7f7f7;text-transform:uppercase;cursor:pointer}button:disabled{cursor:not-allowed;opacity:.8}
